import { CLASS_NAMES, TYPES } from "../constants"
import { HighlightItem } from "./generate-rpjson-stylesheet"

type Props = {
  findIndex?: number
  selectedIndex?: number
  selectedStyle?: object
}

export const highlightFinds = (
  style: object = {},
  { selectedIndex, selectedStyle = {}, findIndex = 0 }: Props = {},
): Array<HighlightItem> => {
  const defaultStyle = {
    className: `&  .${CLASS_NAMES.TYPE_PREFIX}${TYPES.FIND}-${findIndex}`,
    style,
  }
  if (selectedIndex === undefined) return [defaultStyle]
  return [
    {
      style: selectedStyle,
      className: `& .${CLASS_NAMES.TYPE_PREFIX}${TYPES.FIND}-${findIndex}.${CLASS_NAMES.ID_PREFIX}${CLASS_NAMES.FIND_PREFIX}${findIndex}-${selectedIndex}`,
    },
    defaultStyle,
  ]
}
